'use client';

import React, { useState } from 'react';
import { Globe, ArrowRightLeft, Database, Info, FileSearch, Search, Clock, Shield } from 'lucide-react';
import { countries, treatyData } from '@/data/treaties';
import Link from 'next/link';

export default function IntelligencePage() {
  const [origin, setOrigin] = useState('');
  const [destination, setDestination] = useState('');
  const [result, setResult] = useState<any>(null);

  const handleSearch = () => {
    if (!origin || !destination) return;
    
    // Search logic: matches 'OriginName-DestinationName'
    const pairKey = `${origin}-${destination}`;
    const data = treatyData.find(t => t.pair === pairKey);
    setResult(data || 'not_found');
  };

  return (
    <div className="min-h-screen bg-[#FDFCFB] text-black pb-32 font-sans">
      
      {/* 1. HERO SECTION (Translated to English) */}
      <section className="max-w-6xl mx-auto pt-24 pb-12 px-8 text-center space-y-6">
        <div className="inline-flex items-center gap-3 px-6 py-2 rounded-full bg-black text-[#D4AF37] text-[12px] font-black uppercase tracking-[0.4em]">
          <Database className="w-4 h-4" /> Global Intelligence Matrix
        </div>
        <h1 className="text-6xl font-serif italic leading-tight text-black">
          Treaty <span className="text-[#D4AF37]">Reference.</span>
        </h1>
        <p className="max-w-2xl mx-auto text-gray-400 font-light italic text-[17px] leading-relaxed">
          Standard Withholding Tax (WHT) rates for thousands of global jurisdictional pairs. 
          Instant, accurate, and free for initial professional reference.
        </p>
      </section>

      {/* 2. SEARCH INTERFACE (Translated to English) */}
      <section className="max-w-5xl mx-auto px-8 mb-16">
        <div className="bg-white border border-gray-100 rounded-[3.5rem] p-10 shadow-2xl shadow-gray-100/50 flex flex-col md:flex-row items-center gap-8">
          
          <div className="w-full space-y-3">
            <label className="text-[11px] font-black uppercase tracking-widest text-gray-400 ml-4">Source Jurisdiction</label>
            <select 
              value={origin}
              onChange={(e) => setOrigin(e.target.value)}
              className="w-full bg-[#FDFCFB] border border-gray-100 rounded-2xl py-5 px-8 text-[15px] focus:outline-none focus:border-[#D4AF37] appearance-none cursor-pointer"
            >
              <option value="">Select Country...</option>
              {countries.map(c => <option key={c.code} value={c.name}>{c.flag} {c.name}</option>)}
            </select>
          </div>

          <div className="hidden md:block pt-6">
            <ArrowRightLeft className="text-[#D4AF37] opacity-30" size={28} />
          </div>

          <div className="w-full space-y-3">
            <label className="text-[11px] font-black uppercase tracking-widest text-gray-400 ml-4">Target Jurisdiction</label>
            <select 
              value={destination}
              onChange={(e) => setDestination(e.target.value)}
              className="w-full bg-[#FDFCFB] border border-gray-100 rounded-2xl py-5 px-8 text-[15px] focus:outline-none focus:border-[#D4AF37] appearance-none cursor-pointer"
            >
              <option value="">Select Country...</option>
              {countries.map(c => <option key={c.code} value={c.name}>{c.flag} {c.name}</option>)}
            </select>
          </div>

          <div className="pt-6 w-full md:w-auto">
            <button 
              onClick={handleSearch}
              className="w-full md:w-auto px-12 py-5 bg-black text-[#D4AF37] rounded-2xl text-[13px] font-black uppercase tracking-[0.2em] hover:bg-[#D4AF37] hover:text-black transition-all shadow-xl shadow-gold/10 flex items-center justify-center gap-3"
            >
              <Search size={18} /> Search Data
            </button>
          </div>
        </div>
      </section>

      {/* 3. DYNAMIC RESULT SECTION */}
      <section className="max-w-5xl mx-auto px-8">
        {!result ? (
          <div className="text-center py-24 border-2 border-dashed border-gray-50 rounded-[4rem] bg-white/30">
            <Globe className="mx-auto text-gray-100 mb-6" size={48} />
            <p className="text-gray-400 italic font-light">Select origin and target jurisdictions to view treaty summary.</p>
          </div>
        ) : result === 'not_found' ? (
          <div className="text-center py-20 bg-red-50 rounded-[3rem] border border-red-100">
            <p className="text-red-400 italic">Specific data not available in the free database. <br/>Use <strong>Pro Lab</strong> for a deep-dive analysis.</p>
          </div>
        ) : (
          <div className="bg-white border border-gray-100 rounded-[4rem] overflow-hidden shadow-sm animate-in fade-in slide-in-from-bottom duration-700">
            
            {/* Header Result */}
            <div className="p-10 bg-black flex flex-col md:flex-row justify-between items-center gap-6">
              <div className="space-y-2">
                <p className="text-[10px] font-black uppercase tracking-[0.4em] text-gray-500">Treaty Summary</p>
                <h3 className="text-white font-serif italic text-3xl">{origin} <span className="text-[#D4AF37] mx-2">to</span> {destination}</h3>
              </div>
              <div className="px-6 py-2 bg-[#D4AF37]/10 rounded-full border border-[#D4AF37]/30 text-[#D4AF37] text-[11px] font-black uppercase tracking-widest">
                {result.status}
              </div>
            </div>
            
            {/* Main Stats Grid */}
            <div className="grid md:grid-cols-3 divide-y md:divide-y-0 md:divide-x divide-gray-50">
              <div className="p-12 space-y-4 hover:bg-[#FDFCFB] transition-colors">
                <p className="text-[11px] font-black uppercase tracking-widest text-gray-400">Dividends</p>
                <p className="text-5xl font-serif italic text-black">{result.dividend}</p>
              </div>
              <div className="p-12 space-y-4 hover:bg-[#FDFCFB] transition-colors">
                <p className="text-[11px] font-black uppercase tracking-widest text-gray-400">Interest</p>
                <p className="text-5xl font-serif italic text-black">{result.interest}</p>
              </div>
              <div className="p-12 space-y-4 hover:bg-[#FDFCFB] transition-colors">
                <p className="text-[11px] font-black uppercase tracking-widest text-gray-400">Royalties</p>
                <p className="text-5xl font-serif italic text-black">{result.royalty}</p>
              </div>
            </div>

            {/* Additional Info: Residency & Article */}
            <div className="grid md:grid-cols-2 bg-gray-50/50 border-t border-gray-50 divide-y md:divide-y-0 md:divide-x divide-gray-100">
              <div className="p-8 flex items-center gap-6">
                <div className="w-12 h-12 bg-white rounded-xl flex items-center justify-center shadow-sm">
                  <Clock className="text-[#D4AF37]" size={20} />
                </div>
                <div>
                  <p className="text-[10px] font-black uppercase tracking-widest text-gray-400">Residency Rule</p>
                  <p className="text-[16px] font-serif italic text-black">{result.residency}</p>
                </div>
              </div>
              <div className="p-8 flex items-center gap-6">
                <div className="w-12 h-12 bg-white rounded-xl flex items-center justify-center shadow-sm">
                  <Shield className="text-[#D4AF37]" size={20} />
                </div>
                <div>
                  <p className="text-[10px] font-black uppercase tracking-widest text-gray-400">Reference Article</p>
                  <p className="text-[16px] font-serif italic text-black">{result.article}</p>
                </div>
              </div>
            </div>

            {/* Footer Result & Bridge to Pro */}
            <div className="p-10 bg-white flex flex-col md:flex-row justify-between items-center gap-8 border-t border-gray-50">
              <div className="flex items-start gap-4 text-[14px] text-gray-500 italic font-light max-w-xl leading-relaxed">
                <Info size={20} className="text-[#D4AF37] shrink-0 mt-1" /> 
                <p>Strategic Note: <span className="text-black">{result.notes}</span></p>
              </div>
              <Link 
                href="/pro" 
                className="flex items-center gap-3 text-[12px] font-black uppercase tracking-widest bg-black text-[#D4AF37] px-8 py-5 rounded-2xl hover:bg-[#D4AF37] hover:text-black transition-all shadow-lg"
              >
                <FileSearch size={18} /> Run AI Deep Analysis (Pro)
              </Link>
            </div>
          </div>
        )}
      </section>

      {/* FOOTER CALL TO ACTION (Translated to English) */}
      <section className="max-w-4xl mx-auto mt-24 px-8 text-center border-t border-gray-50 pt-20">
        <h4 className="text-[14px] font-black uppercase tracking-[0.4em] text-gray-300 mb-8">Legal Framework</h4>
        <p className="text-[13px] text-gray-400 leading-relaxed max-w-2xl mx-auto italic font-light">
          Data presented reflects standard bilateral Double Taxation Avoidance Agreement (DTAA) rates. 
          For specialized Permanent Establishment (PE) or Limitation of Benefits (LOB) analysis, please utilize the Pro Laboratory.
        </p>
      </section>
    </div>
  );
}